<template>
  <ChartWrapper :title="title" :options="heatMapChartOptions" :series="xyData">
    <template #header>
      <slot name="header"></slot>
    </template>
  </ChartWrapper>
</template>

<script>
import { merge } from "lodash-es";
import ChartWrapper from "./components/ChartWrapper.vue";

export default {
  name: "HeatMapChart",

  components: { ChartWrapper },

  props: {
    title: {
      type: String,
      required: true,
    },

    xyData: {
      type: Array,
      required: true,
    },

    options: {
      type: Object,
      default: () => {},
    },
  },

  data() {
    return {
      heatMapChartOptions: {
        chart: {
          type: "heatmap",
        },
        plotOptions: {
          heatmap: {
            useFillColorAsStroke: true,
          },
        },
      },
    };
  },

  watch: {
    options: {
      immediate: true,
      deep: true,
      handler() {
        merge(this.heatMapChartOptions, this.options);
      },
    },
  },
};
</script>

<style lang="scss" scoped></style>
