<template>
  <ChartWrapper :title="title" :options="rangeChartOptions" :series="xyData">
    <template #header>
      <slot name="header"></slot>
    </template>
  </ChartWrapper>
</template>

<script>
import { merge } from "lodash-es";
import { mapState } from "vuex";
import ChartWrapper from "./components/ChartWrapper.vue";

export default {
  name: "RangeChart",

  components: { ChartWrapper },

  props: {
    title: {
      type: String,
      required: true,
    },

    isHorizontal: {
      type: Boolean,
      default: false,
    },

    xyData: {
      type: Array,
      required: true,
    },

    options: {
      type: Object,
      default: () => {},
    },
  },

  data() {
    return {
      rangeChartOptions: {
        chart: {
          type: "rangeBar",
        },
        plotOptions: {
          bar: {
            horizontal: this.isHorizontal,
          },
        },
      },
    };
  },

  computed: {
    ...mapState(["darkTheme"]),
  },

  watch: {
    options: {
      immediate: true,
      deep: true,
      handler() {
        merge(this.rangeChartOptions, this.options);
      },
    },
  },
};
</script>

<style lang="scss" scoped></style>
